<template>
	<view>	
		<view class="cu-card  article no-card" v-if="title">
			<view class="cu-item shadow">
				<view class="title text-blod">
					{{title}} 
				</view>
				<view class="cu-tag margin-tb-sm">
					发表于 {{updateTime}} 
				</view>
				<view class="content solid-top text-xl" >
					<view class="margin-top-sm" v-html="content">
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import notice from '../../api/notice.js'
	export default{
		data(){
			return{
				title:'',
				content:'',
				updateTime:'',
			}
		},
		async onLoad(options) {
			// 根据Id 获取通知与公告信息 
			const [err,res] = await notice.noticeById(options.id);
			if(res.data.code === 200){
				this.noticeId = res.data.data.id;
				this.title = res.data.data.title;
				this.content = res.data.data.content;
				this.updateTime = res.data.data.updateTime;
			}
			console.log(res);
		}
	}
</script>

<style scoped>
	.title{
		font-size: 26px !important; 
	}
</style>
